import React, { useState } from "react";
import { Link } from "react-router-dom";
import firebase from "../../Firebase";

import Btn from "../UI/button/Btn";
import "./Signup.css";
function Signup() {
  const [userData, setUserData] = useState({
    email: "",
    name: "",
    phone: "",
    password: "",
    repassword: "",
  });

  const { email, name, phone, password, repassword } = userData;

  const handleOnChange = (event) => {
    const { name, value } = event.target;
    setUserData({ ...userData, [name]: value });
  };

  const handleCreateAccount = async (event) => {
    if (email && name && phone && password && repassword) {
      const todoRef = firebase.database().ref("myDatabaseRecords");
      todoRef.push(userData);

      if (todoRef) {
        alert("Account Created successfully");
      } else {
        alert("Unable to create account right now !");
      }
    } else {
      alert("Please fill all entries !");
    }
  };

  return (
    <div className="signup-container">
      <div className="signup">
        <form action="" method="post">
          <div className="input">
            <label htmlFor="">Email : </label>
            <input
              type="text"
              value={email}
              name="email"
              onChange={handleOnChange}
            />
          </div>
          <div className="input">
            <label htmlFor="">Name : </label>
            <input
              type="text"
              value={name}
              name="name"
              onChange={handleOnChange}
            />
          </div>
          <div className="input">
            <label htmlFor="">Phone : </label>
            <input
              type="text"
              value={phone}
              name="phone"
              onChange={handleOnChange}
            />
          </div>
          <div className="input">
            <label htmlFor="">Password : </label>
            <input
              type="text"
              value={password}
              name="password"
              onChange={handleOnChange}
            />
          </div>
          <div className="input">
            <label htmlFor="">ReEnter Password : </label>
            <input
              type="text"
              value={repassword}
              name="repassword"
              onChange={handleOnChange}
            />
            {password !== repassword ? (
              <p style={{ color: "red" }}>Password didn't matched</p>
            ) : null}
          </div>
          <Btn lable="Signup" onClick={handleCreateAccount} />
          <div className="link">
            {<Link to="/e-commerce-website/login">Click to Go on Login page</Link>}
          </div>
        </form>
      </div>
    </div>
  );
}

export default Signup;
